<template>
  <div class="fivePage">
    <p class="title" v-on:click="handlePage()">fivePage</p>

    <p v-on:click="handleChangeState()">第五页第五页第五页</p>
    <component :is='myComponent'></component>
    <layer-comp></layer-comp>

  </div>
</template>

<script>
  import "./fivePage.scss";

  import layerComp from "@/components/layer/layer.vue";

  export default {

    data() {
      // console.log("---------- data -----------");

      let self = this;
      return {
        myComponent: "componentForAlive",
      }

    },

    props: {},

    created() {

      // console.log("---------- created -----------");

    },

    updated() {

      // console.log("---------- updated -----------");

    },

    components: {

      // console.log("---------- components -----------");

      componentForAlive(resolve, reject) {

        require(['@/components/componentForAlive/componentForAlive.vue'], resolve);

      },

      layerComp,

    },





    mounted() {

      // console.log("---------- mounted -----------");

      let self = this;



      document.styleSheets[0].addRule(".changeColor:active", "background: #e6f5ff!important", 0);

    },



    watch: {},

    methods: {

      handleChangeState() {

        this.$store.dispatch('compCtro/changeLayerState');

      },

      handlePage() {

        this.$router.push('/');

      },

      handleNoop() {

        //noop

      },

    },



  }
</script>



